import { Divider, Image, Space } from "antd";
import { Link, NavLink } from "react-router-dom";
import logo from "../../assets/logo.png";
import Function from "../sider/function";
import { Container, Explore, Line, Logo, Main, Menus } from "./style";

export default function SiderPanel() {
  return (
    <Container width={60}>
      <Main>
        <Menus>
          <Logo>
            <Link to="/">
              <Image src={logo} width={36} preview={false} />
            </Link>
          </Logo>
          <Line>
            <Divider />
          </Line>
          <Space direction="vertical" align="center" size={20}>
            <NavLink to="/">
              {({ isActive, isPending }) => (
                <Function type="icon-panel" active={isActive} title="" />
              )}
            </NavLink>

            <NavLink to="/component">
              {({ isActive, isPending }) => (
                <Function
                  type="icon-component"
                  active={isActive}
                  title=""
                />
              )}
            </NavLink>

            <NavLink to="/exception">
              {({ isActive, isPending }) => (
                <Function
                  active={isActive}
                  type="icon-exception"
                  title=""
                  badge={5}
                  badgeStatus="error"
                />
              )}
            </NavLink>

            <NavLink to="/openapi">
              {({ isActive, isPending }) => (
                <Function type="icon-openapi" active={isActive} title="" />
              )}
            </NavLink>
          </Space>
          <Line>
            <Divider style={{ margin: "20px 0" }} />
          </Line>
          <Space direction="vertical" align="center" size={20}>
            <NavLink to="/starter">
              {({ isActive, isPending }) => (
                <Function type="icon-starter" active={isActive} title="" />
              )}
            </NavLink>

            <NavLink to="/generate">
              {({ isActive, isPending }) => (
                <Function
                  type="icon-code-generate"
                  active={isActive}
                  title=""
                />
              )}
            </NavLink>

            <NavLink to="/systeminfo">
              {({ isActive, isPending }) => (
                <Function
                  type="icon-system-info"
                  active={isActive}
                  title=""
                />
              )}
            </NavLink>
          </Space>
          <Line>
            <Divider style={{ margin: "20px 0" }} />
          </Line>
          <Space direction="vertical" align="center" size={20}>
            <NavLink to="/explore">
              {({ isActive, isPending }) => (
                <Function
                  type="icon-explore"
                  active={isActive}
                  title=""
                  style={{ color: "#52c41a" }}
                />
              )}
            </NavLink>
          </Space>
        </Menus>
        <Explore>
          <Space direction="vertical" align="center" size={20}>
            <NavLink to="/setting">
              {({ isActive, isPending }) => (
                <Function type="icon-setting" active={isActive} title="" />
              )}
            </NavLink>
          </Space>
        </Explore>
      </Main>
    </Container>
  );
}
